.ChatViewer main {
    display: flex;
    justify-content: center;  
    align-items: center;      
    height: 80vh;            
  }
  
  .message-viewer {
      display: flex;
      justify-content: center; 
      flex-direction: column;
      align-self: center;
      gap: 10px;
      padding: 20px;
    }
  
    .message-box {
      max-height: 500px;
      max-width: 500px;; 
      overflow-y: auto; 
      padding: 10px;
      background-color: #f4f4f4; 
      border-radius: 10px;
    }
    
    .message {
      max-width: 60%;
      padding: 10px 15px;
      border-radius: 18px;
      position: relative;
      font-size: 18px;
      line-height: 1.4;
      margin-bottom: 10px;
    }
    
    .message.shelter {
        align-self: flex-end;
        background-color: #007aff; /* blue */
        color: white;
      }
      
      .message.user {
        align-self: flex-start;
        background-color: #e5e5ea; /* gray */
        color: black;
      }